import Sidebar from "../component/Sidebar";
import { Outlet } from "react-router-dom";
import { useNavigate, useLocation } from "react-router-dom";
import API from "../services/api";
import { useEffect, useState } from "react";
import { logout } from "../services/auth";
import { ToastContainer } from "react-toastify";
import { toast } from "react-toastify";

const AppLayout = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const location = useLocation();

  const handleLogout = async (e) => {
    e.preventDefault();
    try {
      await logout();
      navigate("/login");
    } catch (err) {
      console.error(err);
      toast.error(
        "Logout gagal: " + (err.response?.data?.message || "Terjadi kesalahan")
      );
    }
  };

  const getPageTitle = () => {
    if (location.pathname.includes("/dashboard")) return "Dashboard";
    if (location.pathname.includes("/leads")) return "Database";
    if (location.pathname.includes("/user")) return "User";
    return "Dashboard";
  };

  useEffect(() => {
    API.get("/me")
      .then((res) => {
        setUser(res.data);
        localStorage.setItem("auth", JSON.stringify(res.data));
      })
      .catch((err) => {
        console.error("Gagal ambil data auth:", err);
        setUser(null);
        localStorage.removeItem("auth");
      });
  }, []);

  return (
    <div className="d-flex">
      <Sidebar />
      <div className="flex-grow-1 main-content">
        <nav className="navbar navbar-expand bg-light shadow-sm px-3">
          <div className="container-fluid ms-3">
            <span className="navbar-brand fs-4 fw-semibold text-dark">
              {getPageTitle()}
            </span>
            <div className="dropdown ms-auto">
              <button
                className="btn btn-light d-flex align-items-center gap-2 rounded px-3 py-2 shadow dropdown-toggle"
                type="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <i className="bi bi-person-circle fs-5"></i>
                <span className="fw-medium">
                  {user
                    ? `Hai, ${
                        user.name.length > 8
                          ? user.name.slice(0, 7) + "..."
                          : user.name
                      }`
                    : "Memuat..."}
                </span>
              </button>

              <ul className="dropdown-menu dropdown-menu-end">
                <li className="d-flex align-items-center mx-3 ">
                  <div
                    className="rounded-circle bg-secondary d-flex justify-content-center align-items-center me-2"
                    style={{ width: "40px", height: "40px" }}
                  >
                    <i className="fa-solid fa-user text-white"></i>
                  </div>
                  <div>
                    <div className="fw-semibold">
                      {user ? `${user.name}` : " "}
                    </div>
                    <div className="text-muted small">
                      {user ? `${user.email}` : " "}
                    </div>
                  </div>
                </li>

                <hr className="my-2" />
                <li>
                  <button
                    className="dropdown-item d-flex align-items-center text-danger"
                    onClick={handleLogout}
                  >
                    <i className="fa-solid fa-arrow-right-from-bracket me-2"></i>{" "}
                    Log out
                  </button>
                </li>
              </ul>
            </div>
          </div>
        </nav>

        <main className="p-4">
          <Outlet />
        </main>
      </div>
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default AppLayout;
