import React, { useEffect, useState } from "react";
import API from "../../services/api";

export default function LeadModal({
  modalRef,
  form,
  editId,
  onChange,
  onSubmit,
  submitLoading,
  onCancelEdit,
  validated,
  errors,
}) {
  // const [mos, setMos] = useState([]);
  // const [tlps, setTlps] = useState([]);
  // const [statuses, setStatuses] = useState([]);
  const [positions, setPosition] = useState([]);

  useEffect(() => {
    const fetchMaster = async () => {
      try {
        const positionRes = await API.get("/position");
        setPosition(positionRes.data);
      } catch (err) {
        console.error("Gagal load master:", err);
      }
    };
    fetchMaster();
  }, []);
  // useEffect(() => {
  //   const fetchMaster = async () => {
  //     try {
  //       const [moRes, tlpRes, statusRes] = await Promise.all([
  //         API.get("/mos"),
  //         API.get("/tlps"),
  //         API.get("/lead-statuses"),
  //       ]);

  //       setMos(moRes.data);
  //       setTlps(tlpRes.data);
  //       setStatuses(statusRes.data);
  //     } catch (err) {
  //       console.error("Gagal load master:", err);
  //     }
  //   };

  //   fetchMaster();
  // }, []);

  return (
    <form
      noValidate
      className={validated ? "was-validated" : ""}
      onSubmit={onSubmit}
    >
      <div
        className="modal fade"
        id="leadModal"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex="-1"
        aria-labelledby="modalLabel"
        ref={modalRef}
      >
        <div className="modal-dialog modal-lg modal-lead">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="modalLabel">
                {editId ? "Edit Lead" : "Tambah Lead"}
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              />
            </div>
            <div className="modal-body">
              <div className="row">
                {/* <div className="col-md-6 mb-2">
                  <input
                    type="date"
                    name="date"
                    value={form.date}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Tanggal"
                    required
                  />
                  <div className="invalid-feedback">Tanggal wajib diisi.</div>
                </div> */}

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="name"
                    value={form.name}
                    onChange={onChange}
                    className={`form-control ${
                      errors.name ? "is-invalid" : ""
                    }`}
                    placeholder="Nama"
                    required
                  />
                  <div className="invalid-feedback">
                    {errors.name || "Nama wajib diisi."}
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="phone_number"
                    value={form.phone_number}
                    onChange={onChange}
                    className={`form-control ${
                      errors.phone_number ? "is-invalid" : ""
                    }`}
                    placeholder="Nomor Telepon"
                    required
                  />
                  <div className="invalid-feedback">
                    {errors.phone_number || "Nomor Telepon wajib diisi."}
                  </div>
                </div>

                {/* <div className="col-md-6 mb-2">
                  <input
                    type="email"
                    name="email"
                    value={form.email}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Email"
                    required
                  />
                  <div className="invalid-feedback">Email wajib diisi.</div>
                </div> */}
                <div className="col-md-6 mb-2">
                  <select
                    className="form-select"
                    name="klpd"
                    value={form.klpd || ""}
                    onChange={onChange}
                    required
                  >
                    <option value="">-- Pilih KLPD --</option>
                    <option value="Kementerian">Kementerian</option>
                    <option value="Lembaga">Lembaga</option>
                    <option value="Pemerintah Daerah">Pemerintah Daerah</option>
                    <option value="Belum diketahui">Belum diketahui</option>
                  </select>
                  <div className="invalid-feedback">KLPD wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <select
                    className="form-select"
                    name="position_id"
                    value={form.position_id || ""}
                    onChange={onChange}
                    required
                  >
                    <option value="">-- Pilih Jabatan --</option>
                    {positions.map((position) => (
                      <option key={position.id} value={position.id}>
                        {position.initials}
                      </option>
                    ))}
                  </select>
                  <div className="invalid-feedback">Jabatan wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="institution_or_business"
                    value={form.institution_or_business}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Instansi / Satker"
                    required
                  />
                  <div className="invalid-feedback">
                    Instansi / Satker wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <textarea
                    type="text"
                    name="location"
                    value={form.location}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Lokasi"
                    required
                    rows={4}
                  />
                  <div className="invalid-feedback">Lokasi wajib diisi.</div>
                </div>

                {/* 
                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="product_need"
                    value={form.product_need}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Kebutuhan Produk"
                    required
                  />
                  <div className="invalid-feedback">
                    Kebutuhan Produk wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="product_category"
                    value={form.product_category}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Kategori Produk"
                    required
                  />
                  <div className="invalid-feedback">
                    Kategori Produk wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <select
                    className="form-select"
                    name="government_or_private"
                    value={form.government_or_private || ""}
                    onChange={onChange}
                    required
                  >
                    <option value="">-- Pilih Jenis Instansi --</option>
                    <option value="Pemerintah">Pemerintah</option>
                    <option value="Swasta">Swasta</option>
                  </select>
                  <div className="invalid-feedback">
                    Jenis Instansi wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="lead_source"
                    value={form.lead_source}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Sumber Lead"
                    required
                  />
                  <div className="invalid-feedback">
                    Sumber Lead wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="company"
                    value={form.company}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Perusahaan"
                    required
                  />
                  <div className="invalid-feedback">
                    Perusahaan wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <select
                    className="form-select"
                    name="mo_id"
                    value={form.mo_id || ""}
                    onChange={onChange}
                    required
                  >
                    <option value="">-- Pilih MO --</option>
                    {mos.map((mo) => (
                      <option key={mo.id} value={mo.id}>
                        {mo.name}
                      </option>
                    ))}
                  </select>
                  <div className="invalid-feedback">MO wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <select
                    className="form-select"
                    name="tlp_id"
                    value={form.tlp_id || ""}
                    onChange={onChange}
                    required
                  >
                    <option value="">-- Pilih TLP --</option>
                    {tlps.map((tlp) => (
                      <option key={tlp.id} value={tlp.id}>
                        {tlp.name}
                      </option>
                    ))}
                  </select>
                  <div className="invalid-feedback">TLP wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="purchasing"
                    value={form.purchasing}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Purchasing"
                    required
                  />
                  <div className="invalid-feedback">
                    Purchasing wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <select
                    className="form-select"
                    name="lead_status_id"
                    value={form.lead_status_id || ""}
                    onChange={onChange}
                    required
                  >
                    <option value="">-- Pilih Status --</option>
                    {statuses.map((status) => (
                      <option key={status.id} value={status.id}>
                        {status.name}
                      </option>
                    ))}
                  </select>
                  <div className="invalid-feedback">Status wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <select
                    className="form-select"
                    name="process"
                    value={form.process || ""}
                    onChange={onChange}
                    required
                  >
                    <option value="">-- Pilih Proses --</option>
                    <option value="Tidak SPK">Tidak SPK</option>
                    <option value="On Progress">On Progress</option>
                    <option value="SPK">SPK</option>
                  </select>
                  <div className="invalid-feedback">Proses wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="text"
                    name="planned_clicks"
                    value={form.planned_clicks}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Rencana Klik"
                    required
                  />
                  <div className="invalid-feedback">
                    Rencana Klik wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="number"
                    name="budget"
                    value={form.budget}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Pagu"
                    required
                  />
                  <div className="invalid-feedback">Pagu wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="number"
                    name="rab"
                    value={form.rab}
                    onChange={onChange}
                    className="form-control"
                    placeholder="RAB"
                    required
                  />
                  <div className="invalid-feedback">RAB wajib diisi.</div>
                </div>

                <div className="col-md-6 mb-2">
                  <input
                    type="number"
                    name="contract_value"
                    value={form.contract_value}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Nominal SPK"
                    required
                  />
                  <div className="invalid-feedback">
                    Nominal SPK wajib diisi.
                  </div>
                </div>

                <div className="col-md-6 mb-2">
                  <span className="input-group-text" disabled>
                    {form.profit}%
                  </span>
                  <div className="invalid-feedback">Profit wajib diisi.</div>
                </div>

                <div className="col-12 mb-2">
                  <textarea
                    name="description"
                    value={form.description}
                    onChange={onChange}
                    className="form-control"
                    placeholder="Keterangan"
                    required
                    rows={4}
                  />
                  <div className="invalid-feedback">
                    Keterangan wajib diisi.
                  </div>
                </div> */}
              </div>
            </div>
            <div className="modal-footer">
              {editId && (
                <button
                  type="button"
                  className="btn btn-secondary me-auto"
                  onClick={onCancelEdit}
                >
                  Batal Edit
                </button>
              )}
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
              <button
                type="submit"
                className="btn btn-primary"
                disabled={submitLoading}
              >
                {submitLoading ? "Memproses..." : editId ? "Update" : "Tambah"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </form>
  );
}
