import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import { getAuthUser} from "../services/auth"
import "./Sidebar.css";

const collections = [
  { icon: "fa-solid fa-gauge me-2", label: "Dashboard", path: "/dashboard" },
  { icon: "fa-solid fa-folder-open me-2", label: "Database", path: "/leads" },
  { icon: "fa-solid fa-user me-2", label: "Users", path: "/users" },
];

const Sidebar = () => {
  const location = useLocation();
  const [collapsed, setCollapsed] = useState(false);
  const auth = getAuthUser();

  useEffect(() => {
    const handleResize = () => {
      setCollapsed(window.innerWidth < 768);
    };

    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return (
    <div
      className={`sidebar d-flex flex-column ${collapsed ? "collapsed" : ""}`}
    >
      <div className="sidebar-header d-flex align-items-center justify-content-between p-3">
        <div className="d-flex align-items-center">
          <img
            src="/img/logo.png"
            alt="Logo"
            className="rounded-circle me-4"
            style={{ width: "30px", height: "30px" }}
          />
          {!collapsed && (
            <div>
              <strong>Sariling Group</strong>
            </div>
          )}
        </div>

        <button
          className="btn btn-sm btn-carousel-sidebar"
          onClick={() => setCollapsed(!collapsed)}
        >
          <i className="fa-solid fa-bars text-dark"></i>
        </button>
      </div>

      <ul className="nav flex-column px-2">
        {collections.map((item) => {
          // Jika item adalah "Users" dan role bukan admin, jangan render
          if (item.label === "Users" && auth?.role !== "admin") return null;

          return (
            <li key={item.path} className="nav-item mb-1">
              <Link
                to={item.path}
                className={`nav-link d-flex align-items-center ${
                  location.pathname === item.path ? "active" : "text-white"
                }`}
                data-tooltip={collapsed ? item.label : ""}
              >
                <span className="icon fs-5">
                  <i className={item.icon}></i>
                </span>
                {!collapsed && <span className="ms-2">{item.label}</span>}
              </Link>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default Sidebar;
