import { useState } from "react";
import {
  createColumnHelper,
  getCoreRowModel,
  useReactTable,
  flexRender,
} from "@tanstack/react-table";
import dayjs from "dayjs";
import "dayjs/locale/id";
import Tippy from "@tippyjs/react";
import "tippy.js/dist/tippy.css";
import { getAuthUser } from "../../services/auth";
import "./Lead.css";
dayjs.locale("id");

export default function LeadTable({ leads, onEdit, onDelete, tableLoading, page, perPageSize }) {
  const columnHelper = createColumnHelper();
  const auth = getAuthUser();

  const columns = [
    columnHelper.display({
      id: "no",
      header: "No",
      size: 50,
      cell: ({ row, table }) => {
        const pageIndex = table.options.meta?.page || 1;
        const perPageSize = table.options.meta?.perPageSize || 10;
        const no = row.index + 1 + (pageIndex - 1) * perPageSize;
        return <div className="text-center">{no}</div>;
      },
    }),

    // columnHelper.accessor("date", {
    //   header: "Tanggal",
    //   size: 200,
    //   cell: ({ getValue }) => {
    //     const rawDate = getValue();
    //     return rawDate ? (
    //       <div className="text-center">
    //         {dayjs(rawDate).format("D MMMM YYYY")}
    //       </div>
    //     ) : (
    //       <div className="text-center">-</div>
    //     );
    //   },
    // }),
    columnHelper.accessor("name", {
      header: "Nama",
      cell: ({ getValue }) => {
        const text = getValue();
        const maxLength = 20;

        return (
          <Tippy content={text} placement="top" maxWidth="auto">
            <span>
              {text?.length > maxLength
                ? text.slice(0, maxLength) + "..."
                : text}
            </span>
          </Tippy>
        );
      },
    }),
    columnHelper.accessor("phone_number", {
      header: "No Telp",
      size: 200,
      cell: ({ getValue }) => {
        const phone = getValue();

        if (!phone || phone.trim() === "") {
          return (
            <div className="d-flex justify-content-center align-items-center text-muted">
              -
            </div>
          );
        }

        const formattedPhone = phone.replace(/\D/g, "");
        const waLink = `https://wa.me/${
          formattedPhone.startsWith("0")
            ? "62" + formattedPhone.slice(1)
            : formattedPhone
        }`;

        return (
          <a
            href={waLink}
            target="_blank"
            rel="noopener noreferrer"
            className="text-success d-flex align-items-center justify-content-center gap-1 text-decoration-none text-center"
          >
            <i className="fa-brands fa-whatsapp fs-3"></i>
            <span>Whatsapp</span>
          </a>
        );
      },
    }),
    columnHelper.accessor("klpd", { header: "KLPD", size: 100 }),
    columnHelper.accessor((row) => row.position?.initials, {
      header: "Jabatan",
      cell: (info) => info.getValue() || "-",
    }),
    // columnHelper.accessor("email", { header: "KLPD", size: 200 }),
    columnHelper.accessor("institution_or_business", {
      header: "Instansi/Satker",
      cell: ({ getValue }) => {
        const text = getValue();
        const maxLength = 20;

        return (
          <Tippy content={text} placement="top" maxWidth="auto">
            <span>
              {text?.length > maxLength
                ? text.slice(0, maxLength) + "..."
                : text}
            </span>
          </Tippy>
        );
      },
    }),

    columnHelper.accessor("location", {
      header: "Lokasi",
      cell: function LocationCell({ getValue }) {
        const text = getValue();
        const maxLength = 20;

        const [expanded, setExpanded] = useState(false);
        if (!text) return "-";

        const isLong = text.length > maxLength;
        const displayText =
          expanded || !isLong ? text : text.slice(0, maxLength) + "...";

        return (
          <div>
            <span>{displayText}</span>
            {isLong && (
              <button
                type="button"
                className="btn btn-sm btn-link ms-2 p-0"
                onClick={(e) => {
                  e.stopPropagation();
                  setExpanded(!expanded);
                }}
              >
                {expanded ? "Sembunyikan" : "Lihat"}
              </button>
            )}
          </div>
        );
      },
    }),

    // columnHelper.accessor("product_need", {
    //   header: "Kebutuhan Product",
    //   size: 200,
    //   cell: ({ getValue }) => {
    //     const text = getValue();
    //     const maxLength = 30;

    //     return (
    //       <Tippy content={text} placement="top" maxWidth="300px">
    //         <span>
    //           {text?.length > maxLength
    //             ? text.slice(0, maxLength) + "..."
    //             : text}
    //         </span>
    //       </Tippy>
    //     );
    //   },
    // }),
    // columnHelper.accessor("product_category", {
    //   header: "Kategori Product",
    //   size: 200,
    //   cell: ({ getValue }) => {
    //     const text = getValue();
    //     const maxLength = 30;

    //     return (
    //       <Tippy content={text} placement="top" maxWidth="300px">
    //         <span>
    //           {text?.length > maxLength
    //             ? text.slice(0, maxLength) + "..."
    //             : text}
    //         </span>
    //       </Tippy>
    //     );
    //   },
    // }),

    // columnHelper.accessor("government_or_private", {
    //   header: "Pemerintah/Swasta",
    //   size: 200,
    // }),
    // columnHelper.accessor("lead_source", { header: "Sumber Leads", size: 200 }),
    // columnHelper.accessor("company", { header: "Perusahaan" }),
    // columnHelper.accessor((row) => row.mo_id?.name, {
    //   header: "MO",
    //   cell: (info) => info.getValue() || "-",
    // }),
    // columnHelper.accessor((row) => row.tlp_id?.name, {
    //   header: "TLP",
    //   cell: (info) => info.getValue() || "-",
    // }),
    // columnHelper.accessor("purchasing", {
    //   header: "Purchasing",
    //   size: 200,
    //   cell: ({ getValue }) => {
    //     const text = getValue();
    //     const maxLength = 30;

    //     return (
    //       <Tippy content={text} placement="top" maxWidth="300px">
    //         <span>
    //           {text?.length > maxLength
    //             ? text.slice(0, maxLength) + "..."
    //             : text}
    //         </span>
    //       </Tippy>
    //     );
    //   },
    // }),
    // columnHelper.accessor((row) => row.lead_status_id?.name, {
    //   header: "Status Leads",
    //   cell: (info) => info.getValue() || "-",
    // }),
    // columnHelper.accessor("process", { header: "Proses" }),
    // columnHelper.accessor("planned_clicks", { header: "Rencana Klik" }),
    // columnHelper.accessor((row) => row.budget, {
    //   header: "Pagu",
    //   size: 300,
    //   cell: (info) => {
    //     const item = info.getValue() || "-";
    //     return <div className="text-center">{item}</div>;
    //   },
    // }),
    // columnHelper.accessor((row) => row.rab, {
    //   header: "RAB",
    //   size: 300,
    //   cell: (info) => {
    //     const item = info.getValue() || "-";
    //     return <div className="text-center">{item}</div>;
    //   },
    // }),
    // columnHelper.accessor((row) => row.contract_value, {
    //   header: "Nominal SPK",
    //   size: 300,
    //   cell: (info) => {
    //     const item = info.getValue() || "-";
    //     return <div className="text-center">{item}</div>;
    //   },
    // }),
    // columnHelper.accessor((row) => row.profit, {
    //   header: "Profit",
    //   cell: (info) => {
    //     const item = info.getValue() || "0";
    //     return <div className="text-center">{item}%</div>;
    //   },
    // }),
    // columnHelper.accessor("description", {
    //   header: "Keterangan",
    //   size: 200,
    //   cell: ({ getValue }) => {
    //     const text = getValue();
    //     const maxLength = 30;

    //     return (
    //       <Tippy content={text} placement="top" maxWidth="300px">
    //         <span>
    //           {text?.length > maxLength
    //             ? text.slice(0, maxLength) + "..."
    //             : text}
    //         </span>
    //       </Tippy>
    //     );
    //   },
    // }),
    auth?.role !== "operational" &&
      columnHelper.display({
        id: "actions",
        header: "Aksi",
        cell: ({ row }) => {
          const item = row.original;
          return (
            <div className="btn-action-lead d-flex gap-1 justify-content-center flex-md-row flex-column">
              <button
                className="btn btn-sm btn-warning"
                onClick={() => onEdit(item)}
              >
                Edit
              </button>
              <button
                className="btn btn-sm btn-danger"
                onClick={() => onDelete(item.id)}
              >
                Hapus
              </button>
            </div>
          );
        },
        meta: {
          isSticky: true,
          className: "size-column-lead",
        },
      }),
  ].filter(Boolean);

  const table = useReactTable({
    data: leads,
    columns,
    meta: {
      page,
      perPageSize,
    },
    getCoreRowModel: getCoreRowModel(),
    manualPagination: true,
    pageCount: Math.ceil(leads.length / 10),
  });

  return (
    <>
      {tableLoading ? (
        <div className="d-flex justify-content-center align-items-center py-5">
          <div className="spinner-border text-success" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      ) : (
        <div style={{ maxHeight: "543px", overflowY: "auto" }}>
          <table className="table table-bordered table-striped">
            <thead>
              {table.getHeaderGroups().map((headerGroup) => (
                <tr key={headerGroup.id}>
                  {headerGroup.headers.map((header) => (
                    <th
                      key={header.id}
                      className={header.column.columnDef.meta?.className || ""}
                      style={{
                        width: header.getSize(),
                        textAlign: "center",
                        position: "sticky",
                        top: 0,
                        background: "white",
                        zIndex: 2,
                      }}
                    >
                      {flexRender(
                        header.column.columnDef.header,
                        header.getContext()
                      )}
                    </th>
                  ))}
                </tr>
              ))}
            </thead>
            <tbody>
              {table.getRowModel().rows?.length > 0 ? (
                table.getRowModel().rows.map((row) => (
                  <tr key={row.id}>
                    {row.getVisibleCells().map((cell) => (
                      <td
                        key={cell.id}
                        style={{
                          width: cell.column.getSize(),
                          minWidth: cell.column.getSize(),
                          maxHeight: "60px",
                          overflow: "hidden",
                          textOverflow: "ellipsis",
                          whiteSpace: "nowrap",
                          position: cell.column.columnDef.meta?.isSticky
                            ? "sticky"
                            : undefined,
                          right: cell.column.columnDef.meta?.isSticky
                            ? 0
                            : undefined,
                          background: "white",
                          zIndex: 1,
                        }}
                      >
                        {flexRender(
                          cell.column.columnDef.cell,
                          cell.getContext()
                        )}
                      </td>
                    ))}
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={columns.length} className="text-center py-3">
                    Tidak ada data
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      )}
    </>
  );
}
