import { useEffect, useRef, useState } from "react";
import { Modal } from "bootstrap";
import { useNavigate } from "react-router-dom";
import API from "../../services/api";
import { toast } from "react-toastify";
import Swal from "sweetalert2";
import { getAuthUser } from "../../services/auth";

import LeadTable from "./LeadTable";
import LeadModal from "./LeadModal";
import ImportModal from "./ImportModal";

const initialForm = {
  id: null,
  // date: "",
  name: "",
  phone_number: "",
  // email: "",
  klpd: "",
  position_id: "",
  institution_or_business: "",
  location: "",
  // product_need: "",
  // product_category: "",
  // government_or_private: "",
  // lead_source: "",
  // company: "",
  // mo_id: "",
  // tlp_id: "",
  // purchasing: "",
  // lead_status_id: "",
  // process: "",
  // planned_clicks: "",
  // budget: "",
  // rab: "",
  // contract_value: "",
  // profit: "",
  // description: "",
};

export default function Leads() {
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploading, setUploading] = useState(false);
  const [tableLoading, setTableLoading] = useState(false);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [exportLoading, setExportLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [validated, setValidated] = useState(false);
  const [search, setSearch] = useState("");
  const [debouncedSearch, setDebouncedSearch] = useState(search);
  const [leads, setLeads] = useState([]);
  const [form, setForm] = useState(initialForm);
  const [editId, setEditId] = useState(null);
  const [page, setPage] = useState(1);
  const [perPage, setPerPage] = useState(10);
  const [totalPages, setTotalPages] = useState(1);
  const [totalData, setTotalData] = useState(0);
  const modalRef = useRef(null);
  const importModalRef = useRef(null);
  const fileInputRef = useRef();
  const navigate = useNavigate();
  const auth = getAuthUser();

  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedSearch(search);
    }, 2000);

    return () => clearTimeout(handler);
  }, [search]);

  useEffect(() => {
    fetchLeads(1, debouncedSearch);
  }, [debouncedSearch]);

  const fetchLeads = async (page = 1, search = "", perPageSize = perPage) => {
    setTableLoading(true);
    try {
      const res = await API.get("/leads", {
        params: {
          page: page,
          per_page: perPageSize,
          search: search,
        },
      });
      setLeads(res.data.data);
      setTotalPages(res.data.meta.last_page);
      setTotalData(res.data.meta.total);
      setTableLoading(false);
    } catch (err) {
      setTableLoading(false);
      toast.error("Sesi habis. Silakan login ulang.");
      navigate("/login");
    }
  };

  const validate = () => {
    const newErrors = {};

    if (!form.name.trim()) {
      newErrors.name = "Nama wajib diisi.";
    }

    if (!form.phone_number) {
      newErrors.phone_number = "Nomor Telepon wajib diisi.";
    } else if (!/^08/.test(form.phone_number)) {
      newErrors.phone_number = "Nomor harus dimulai dengan 08.";
    } else if (form.phone_number.length > 13) {
      newErrors.phone_number = "Nomor maksimal 13 digit.";
    }

    if (!form.klpd) {
      newErrors.klpd = "KLPD wajib diisi.";
    }

    if (!form.position_id) {
      newErrors.position_id = "Jabatan wajib diisi.";
    }

    if (!form.institution_or_business.trim()) {
      newErrors.institution_or_business = "Instansi / Satker wajib diisi.";
    }

    if (!form.location.trim()) {
      newErrors.location = "Lokasi wajib diisi.";
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e) => {
    const { name, value } = e.target;

    const parsedValue =
      name === "phone_number" ? value.replace(/\D/g, "") : value;

    const updatedForm = {
      ...form,
      [name]: ["mo_id", "tlp_id", "lead_status_id"].includes(name)
        ? parseInt(value)
        : parsedValue,
    };

    if (name === "rab" || name === "contract_value") {
      const rab = parseFloat(updatedForm.rab);
      const contract = parseFloat(updatedForm.contract_value);
      if (!isNaN(rab) && rab > 0 && !isNaN(contract)) {
        updatedForm.profit = (((contract - rab) / rab) * 100).toFixed(2);
      } else {
        updatedForm.profit = "";
      }
    }

    setForm(updatedForm);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formEl = e.currentTarget;

    const html5Valid = formEl.checkValidity();
    const customValid = validate();

    if (!html5Valid || !customValid) {
      e.stopPropagation();
      setValidated(true);
      return;
    }
    setSubmitLoading(true);
    try {
      if (editId) {
        await API.put(`/leads/${editId}`, form);
        toast.info("Lead berhasil diperbarui.");
      } else {
        await API.post("/leads", form);
        toast.success("Lead berhasil ditambah.");
      }

      setForm(initialForm);
      setEditId(null);
      fetchLeads();

      const modalInstance =
        Modal.getInstance(modalRef.current) || new Modal(modalRef.current);
      modalInstance.hide();

      document.body.classList.remove("modal-open");
      document.querySelectorAll(".modal-backdrop").forEach((el) => el.remove());

      setValidated(false);
      setErrors({});
      setSubmitLoading(false);
    } catch (err) {
      const errorMessage =
        err.response?.data?.message || err.message || "Gagal menyimpan lead!";
      toast.error(errorMessage);
      setSubmitLoading(false);
    }
  };

  const handleEdit = (lead) => {
    setForm({
      ...lead,
      mo_id: lead.mo_id?.id || "",
      tlp_id: lead.tlp_id?.id || "",
      lead_status_id: lead.lead_status_id?.id || "",
      position_id: lead.position?.id || "",
    });
    setEditId(lead.id);
    new Modal(modalRef.current).show();
  };

  const handleDelete = async (id) => {
    const result = await Swal.fire({
      title: "Yakin?",
      text: "Lead akan dihapus secara permanen.",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "Ya, hapus",
      cancelButtonText: "Batal",
    });
    if (result.isConfirmed) {
      try {
        await API.delete(`/leads/${id}`);
        toast.success("Lead berhasil dihapus!");
        fetchLeads();
      } catch (err) {
        toast.error("Gagal menghapus lead.");
      }
    }
  };

  const handleExport = async () => {
    setExportLoading(true);
    try {
      const res = await API.get("/leads/export", {
        responseType: "blob",
        params: {
          search: search,
        },
      });

      const blob = new Blob([res.data], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      });

      const url = window.URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", "leads.xlsx");
      document.body.appendChild(link);
      link.click();
      link.remove();
      setExportLoading(false);
    } catch (error) {
      setExportLoading(false);
      toast.error("Export gagal: " + error.message);
    }
  };

  const handleImport = async (formData) => {
    try {
      setUploading(true);
      setUploadProgress(0);
      const response = await API.post("/leads/import", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        onUploadProgress: (event) => {
          const percent = Math.round((event.loaded * 100) / event.total);
          setUploadProgress(percent);
        },
      });
      const result = response?.data;

      toast.success(
        `✅ Import selesai!\n\n📥 Ditambahkan : ${result.inserted}\n🔄 Diperbarui  : ${result.updated}\n❌ Gagal       : ${result.failed}`,
        {
          style: { whiteSpace: "pre-line" },
        }
      );

      fetchLeads();
      Modal.getInstance(importModalRef.current)?.hide();
    } catch (err) {
      toast.error(
        "Gagal import: " + (err.response?.data?.message || err.message)
      );
    } finally {
      if (fileInputRef.current) {
        fileInputRef.current.value = "";
      }
      setUploading(false);
      setUploadProgress(0);
    }
  };

  return (
    <div>
      <div className="d-flex justify-content-between align-items-center mb-2">
        <h3>Data Leads</h3>
      </div>
      <div className="d-flex justify-content-end mb-2">
        <div className="col-md-4">
          <div
            className="d-flex border rounded overflow-hidden"
            style={{ height: "38px" }}
          >
            <input
              type="text"
              className="form-control border-0 shadow-none"
              placeholder="Search..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              style={{ flex: 1 }}
            />
            <span className="d-flex align-items-center px-3 border-start bg-white">
              <i className="fa-solid fa-search text-muted"></i>
            </span>
          </div>
        </div>
      </div>
      <div className="d-flex flex-wrap justify-content-end gap-2 mb-2">
        <button
          className="btn btn-primary"
          onClick={() => {
            setEditId(null);
            setForm(initialForm);
            new Modal(modalRef.current).show();
          }}
        >
          <i className="fa-solid fa-plus"></i> Tambah Kontak
        </button>

        {auth?.role !== "operational" && (
          <>
            <button
              onClick={handleExport}
              className="btn btn-success"
              disabled={exportLoading}
            >
              {exportLoading ? (
                "Mengekspor..."
              ) : (
                <>
                  <i className="fa-solid fa-file-download"></i> Export
                </>
              )}
            </button>
            <button
              className="btn btn-warning"
              onClick={() => {
                new Modal(importModalRef.current).show();
              }}
            >
              <i className="fa-solid fa-upload"></i> Import
            </button>
          </>
        )}
      </div>

      <div className="d-flex justify-content-between align-items-center my-3">
        <div>
          <label className="me-2">Tampilkan:</label>
          <select
            className="form-select d-inline-block w-auto"
            value={perPage}
            onChange={(e) => {
              const newSize = Number(e.target.value);
              setPerPage(newSize);
              setPage(1);
              fetchLeads(1, search, newSize);
            }}
          >
            {[10, 50, 100, 500, 1000].map((size) => (
              <option key={size} value={size}>
                {size}
              </option>
            ))}
          </select>
          <span className="ms-2">data per halaman</span>
        </div>
      </div>

      <LeadTable
        leads={leads}
        tableLoading={tableLoading}
        onEdit={handleEdit}
        onDelete={handleDelete}
        page={page}
        perPageSize={perPage}
      />

      <div className="d-flex justify-content-between align-items-center mt-3">
        <span>
          Page {page} of {totalPages} dari {totalData.toLocaleString("id-ID")}
        </span>
        <div>
          <button
            className="btn btn-secondary btn-sm me-2"
            disabled={page === 1}
            onClick={() => {
              const newPage = page - 1;
              setPage(newPage);
              fetchLeads(newPage, search);
            }}
          >
            Previous
          </button>
          <button
            className="btn btn-secondary btn-sm"
            disabled={page === totalPages}
            onClick={() => {
              const newPage = page + 1;
              setPage(newPage);
              fetchLeads(newPage, search);
            }}
          >
            Next
          </button>
        </div>
      </div>
      <ImportModal
        modalRef={importModalRef}
        fileInputRef={fileInputRef}
        onImport={handleImport}
        uploadProgress={uploadProgress}
        uploading={uploading}
      />
      <LeadModal
        modalRef={modalRef}
        form={form}
        editId={editId}
        onChange={handleChange}
        onSubmit={handleSubmit}
        submitLoading={submitLoading}
        validated={validated}
        errors={errors}
        onCancelEdit={() => {
          setEditId(null);
          setForm({ ...form, id: null });
        }}
      />
    </div>
  );
}
